import natsort from "natsort"
import { useEffect, useMemo, useState } from "react"
import Button from "react-bootstrap/Button"
import Form from "react-bootstrap/Form"
import Modal from "react-bootstrap/Modal"
import {
    createProductFilter,
    ProductFilter,
} from "../../entities/ProductFilter"
import { getProductTypeString, ProductType } from "../../entities/ProductType"
import CatalogService from "../../services/CatalogService"

function FilterDialog({
    filter,
    catalog,
    onApply,
    onClose,
}: {
    filter: ProductFilter | undefined
    catalog: CatalogService
    onApply: (filter: ProductFilter) => void
    onClose: () => void
}) {
    let sorter = useMemo(() => natsort(), [])
    let [show, setShow] = useState(false)
    let [vendors, setVendors] = useState([] as string[])
    let [prizeFrom, setPrizeFrom] = useState(0)
    let [prizeTo, setPrizeTo] = useState(0)
    let [nks, setNks] = useState(undefined as boolean | undefined)
    let [types, setTypes] = useState([] as ProductType[])

    useEffect(() => {
        setVendors(filter === undefined ? [] : filter.vendors)
        setPrizeFrom(filter === undefined ? 0 : filter.prizeFrom)
        setPrizeTo(filter === undefined ? 0 : filter.prizeTo)
        setNks(filter === undefined ? undefined : filter.nks)
        setTypes(filter === undefined ? [] : filter.types)
        setShow(filter !== undefined)
    }, [filter])

    return (
        <Modal
            show={show}
            onHide={() => {
                filter = undefined
                setShow(false)
                onClose()
            }}
        >
            <Modal.Header closeButton closeLabel="Cancel">
                <Modal.Title>Configure filter</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                {show ? (
                    <Form>
                        <h4>Vendors</h4>
                        <Form.Group controlId="formVendors">
                            {catalog
                                .getVendors()
                                .sort((a, b) => sorter(a.name, b.name))
                                .map((v) => (
                                    <Form.Check
                                        id={"form-vendor-" + v.id}
                                        type="checkbox"
                                        label={`${v.name} (${
                                            catalog.getProductsByVendor(v.id)
                                                .length
                                        })`}
                                        checked={
                                            vendors.find(
                                                (vs) => vs === v.id
                                            ) !== undefined
                                        }
                                        onChange={(evt) => {
                                            if (evt.target.checked === true) {
                                                if (
                                                    vendors.find(
                                                        (vs) => vs === v.id
                                                    ) === undefined
                                                )
                                                    setVendors([
                                                        ...vendors,
                                                        v.id,
                                                    ])
                                            } else {
                                                if (
                                                    vendors.find(
                                                        (vs) => vs === v.id
                                                    ) !== undefined
                                                )
                                                    setVendors(
                                                        vendors.filter(
                                                            (vs) => vs !== v.id
                                                        )
                                                    )
                                            }
                                        }}
                                    />
                                ))}
                        </Form.Group>
                        <h4>Prize</h4>
                        <Form.Group controlId="formPrize">
                            <Form.Label>From $</Form.Label>
                            <Form.Control
                                type="number"
                                value={prizeFrom}
                                onChange={(evt) => {
                                    let n: number = parseInt(evt.target.value)

                                    if (isNaN(n)) {
                                        setPrizeFrom(0)
                                        return
                                    }
                                    setPrizeFrom(n)
                                }}
                            />
                            <Form.Label>To $</Form.Label>
                            <Form.Control
                                type="number"
                                value={prizeTo}
                                onChange={(evt) => {
                                    let n: number = parseInt(evt.target.value)

                                    if (isNaN(n)) {
                                        setPrizeTo(0)
                                        return
                                    }
                                    setPrizeTo(n)
                                }}
                            />
                        </Form.Group>
                        <h4>NKS Compatibility</h4>
                        <Form.Group controlId="formNks">
                            <Form.Select
                                value={
                                    nks === undefined
                                        ? "disabled"
                                        : nks === false
                                        ? "no"
                                        : "yes"
                                }
                                onChange={(evt) => {
                                    switch (evt.target.value) {
                                        case "disabled":
                                            setNks(undefined)
                                            return
                                        case "no":
                                            setNks(false)
                                            return
                                        case "yes":
                                            setNks(true)
                                            return
                                    }
                                }}
                            >
                                <option value="disabled">Disabled</option>
                                <option value="no">No</option>
                                <option value="yes">Yes</option>
                            </Form.Select>
                        </Form.Group>
                        <h4>Product Type</h4>
                        <Form.Group controlId="formTypes">
                            {Object.keys(ProductType)
                                .filter((t) => !isNaN(parseInt(t)))
                                .map((t) => (
                                    <Form.Check
                                        id={`form-type-${t}`}
                                        type="checkbox"
                                        label={`${getProductTypeString(
                                            parseInt(t) as ProductType
                                        )} (${
                                            catalog.getProducts({
                                                ...createProductFilter(),
                                                enabled: true,
                                                types: [
                                                    parseInt(t) as ProductType,
                                                ],
                                            }).length
                                        })`}
                                        checked={
                                            types.find(
                                                (tn) => tn === parseInt(t)
                                            ) !== undefined
                                        }
                                        onChange={(evt) => {
                                            if (evt.target.checked === true) {
                                                if (
                                                    types.find(
                                                        (tn) =>
                                                            tn === parseInt(t)
                                                    ) === undefined
                                                )
                                                    setTypes([
                                                        ...types,
                                                        parseInt(
                                                            t
                                                        ) as ProductType,
                                                    ])
                                            } else {
                                                if (
                                                    types.find(
                                                        (tn) =>
                                                            tn === parseInt(t)
                                                    ) !== undefined
                                                )
                                                    setTypes(
                                                        types.filter(
                                                            (tn) =>
                                                                tn !==
                                                                parseInt(t)
                                                        )
                                                    )
                                            }
                                        }}
                                    />
                                ))}
                        </Form.Group>
                    </Form>
                ) : (
                    ""
                )}
            </Modal.Body>
            <Modal.Footer>
                <Button
                    variant="secondary"
                    onClick={() => {
                        filter = undefined
                        setShow(false)
                        onClose()
                    }}
                >
                    Cancel
                </Button>
                <Button
                    variant="primary"
                    onClick={() => {
                        setShow(false)
                        onApply({
                            ...(filter as unknown as ProductFilter),
                            vendors: vendors,
                            prizeFrom: prizeFrom,
                            prizeTo: prizeTo,
                            nks: nks,
                            types: types,
                            enabled: true,
                        })
                        filter = undefined
                    }}
                >
                    Apply filter
                </Button>
            </Modal.Footer>
        </Modal>
    )
}

export default FilterDialog
